<template>
	<view class="riskAssessment" :style="{ height: containerHeight, width: screenWidth }" @touchmove.prevent>
		<view>
			<uni-nav-bar style="background-color:#ffffff;color:black;font-weight: bold;" :fixed="false" shadow
				title="Message">
				<!-- 右侧内容（图片） -->
				<template v-slot:right>
					<image class="img" src="../../static/qa/read.png" @click="clearAll"
						style="z-index: 999;width: 30%;height: 40%;" />
				</template>
			</uni-nav-bar>
			<!-- <image @click="clearAll" style="position: fixed;right: 35%;top: 35px;z-index: 999;width: 3%;height: 2%;"
				src="../../static/qa/read.png" class="img"></image> -->
		</view>
		<div>
			<view class="infor-list" id="infor-list" :style="{ height: pageHeight }">
				<scroll-view style="height: 100%;" scroll-y="true" lower-threshold='0px' @scrolltolower="scrolltolower">
					<view class="list" v-for="(item, index) in messageList" :key="index" @click="toggle('center', item)">
						<image :src="imgPath" class="img"></image>
						<view class="list-part">
							<view class="title-time">
								<view style="display: flex; justify-content: space-between;margin-top: 4%;">
									<view class="titleText">
										{{ item.titile }}
										<!-- {{item.titile.length > 13 ? item.titile.slice(0, 12) + '...' : item.titile}} -->
									</view>
									<p class="red-dot-text" v-if="item.readFlag == 0"></p>
									<view style="font-size: 12px;">{{ item.sendTime.slice(0, 10) }}</view>
								</view>
								<view style="color: #999;margin-top: 2%;">
									{{ item.msgContent.length > 65 ? item.msgContent.slice(0, 45) + '...' :
										item.msgContent}}
								</view>
							</view>
						</view>
					</view>
				</scroll-view>
			</view>
		</div>
		<u-popup @touchmove.native.stop.prevent :closeable="true" :show="show" :round="30" mode="center"
			@close="closePopup(popForm)" @open="open">
			<view style="text-align: center;padding: 15px;font-size: 16px;">{{ popForm.titile }}</view>
			<scroll-view :scroll-top="10" style="height: 80% !important;" scroll-y="true">
				<view style="padding: 20rpx;height: 50%; margin-bottom: 10%;">
					<view style="border-radius: 5px;padding: 3%;;word-wrap: break-word;">{{ popForm.msgContent }}</view>
				</view>
			</scroll-view>
		</u-popup>


	</view>
</template>

<script>
let platform = uni.getSystemInfoSync().platform
import {
	USER_INFO_JF,
	USER_INFO
} from "@/common/util/constants"
import {
	getAllMessageList
} from '../task/publicMessage.js'

/**
 * URL 列表
 */
const URL = {
	list: '/testService/qaTestCalendar/getMyAnnouncementSend',
	edit: '/sys/sysAnnouncementSend/editByAnntIdAndUserId',
	readAll: '/sys/sysAnnouncementSend/readAll'
}

export default {
	data() {
		return {
			tip: '上拉加载更多',
			total: 0,
			imgPath: require('../../static/qa/message.png'),
			messageList: [],
			tempMessageList: [],
			connected: false,
			connecting: false,
			socketTask: false,
			msg: false,
			popForm: {},
			params: {
				pageNo: 0,
				pageSize: 10,
			},
			containerHeight: '800px',
			screenWidth: '',
			show: false,
			pageHeight: 0,
		}

	},
	computed: {
		showMsg() {
			if (this.connected) {
				if (this.msg) {
					return '收到消息：' + this.msg
				} else {
					return '等待接收消息'
				}
			} else {
				return '尚未连接'
			}
		}
	},
	onLoad(e) {
		// #ifdef APP-PLUS
		const info = uni.getSystemInfoSync();
		if (info.uniPlatform.toLowerCase() === 'app') {
			console.log("load")
			plus.navigator.setStatusBarStyle("dark")
		}
		this.getScreenHeight()
		// #ifdef APP-PLUS
		this.getMessageList()
	},
	mounted() {
		// #ifdef APP-PLUS
		const info = uni.getSystemInfoSync();
		if (info.uniPlatform.toLowerCase() === 'app') {
			console.log("mounted")
			plus.navigator.setStatusBarStyle("dark")
		}
		// #endif
	},
	methods: {
		clearAll() {
			let that = this
			uni.showModal({
				title: 'Clear All',
				content: 'Are You Sure To Clear All?',
				success: function (res) {
					if (res.confirm) {
						that.$http.put(URL.readAll).then(res => {
							if (res.data.success) {
								uni.showToast({
									title: 'Read All!',
								})
								setTimeout(() => {
									that.getMessageList()
								}, 1000)

							}
						}).catch(e => {
							console.log("请求错误", e)
						})
					} else if (res.cancel) {
						console.log('取消');
					}
				}
			});

			uni.removeTabBarBadge({
				index: 1,
			})
		},
		open() {
			this.show = true
		},
		closePopup(item) {
			//修改当前消息的读取状态
			//并重新渲染页面
			let postData = {
				anntId: item.anntId,
			}
			this.$http.put(URL.edit, postData).then(res => {
				if (res.data.success) {
					this.getMessageList()
				}
			}).catch(e => {
				console.log("请求错误", e)
			})
			this.show = false
		},
		getScreenHeight() {
			uni.getSystemInfo({
				success: (res) => {
					this.containerHeight = res.windowHeight + 'px';
					this.pageHeight = res.windowHeight * 0.93 + 'px'
					this.screenWidth = res.windowWidth + 'px'
				}
			});
		},
		getMessageList(type) {
			let that = this
			that.total = 20

			if (type) {
				//如果不是undefined,说明需要翻页
				that.params.pageNo++
			} else {
				that.params.pageNo = 1
			}
			this.$http.get(URL.list, {
				params: this.params
			}).then(res => {
				if (res.data.success) {
					if (type) {
						this.messageList = [...this.messageList, ...res.data.result.records]
					} else {
						this.messageList = res.data.result.records
					}
					this.$store.dispatch('changeMessageNum', res.data.code)
					if (this.$store.state.messageNum > 0) {
						uni.setTabBarBadge({
							index: 1,
							text: this.$store.state.messageNum > 99 ? '99+' : this.$store.state
								.messageNum.toString(),
						})
					} else {
						uni.removeTabBarBadge({
							index: 1,
						})
					}
				}

			})
		},
		scrolltolower(e) {
			let that = this
			// this.tip = '加载中ing...'
			uni.showToast({
				title: '加载中',
				icon: 'loading',
				duration: 1000
			})
			if (that.messageList.length == that.total) {
				uni.showToast({
					title: '已加载全部数据',
					icon: 'none'
				})
			} else {
				setTimeout(() => {
					// that.messageList = that.messageList.concat(that.tempMessageList)
					that.getMessageList("nextPage");
				}, 100)
			}
		},
		change(e) {
			console.log('当前模式：' + e.type + ',状态：' + e.show);
		},
		toggle(type, item) {
			this.popForm = Object.assign({}, item)
			this.open(type)
		},
		connect() {
			if (this.connected || this.connecting) {
				uni.showModal({
					content: '正在连接或者已经连接，请勿重复连接',
					showCancel: false
				})
				return false
			}
			this.connecting = true
			uni.showLoading({
				title: '连接中...'
			})
			this.socketTask = uni.connectSocket({
				url: 'wss://echo.websocket.org',
				data() {
					return {
						msg: 'Hello'
					}
				},
				// #ifdef MP
				header: {
					'content-type': 'application/json'
				},
				// #endif
				// #ifdef MP-WEIXIN
				method: 'GET',
				// #endif
				success(res) {
					// 这里是接口调用成功的回调，不是连接成功的回调，请注意
				},
				fail(err) {
					// 这里是接口调用失败的回调，不是连接失败的回调，请注意
				}
			})
			console.log(this.socketTask);
			this.socketTask.onOpen((res) => {
				this.connecting = false
				this.connected = true
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '连接成功'
				})
				console.log('onOpen', res);
			})
			this.socketTask.onError((err) => {
				this.connecting = false
				this.connected = false
				uni.hideLoading()
				uni.showModal({
					content: '连接失败，可能是websocket服务不可用，请稍后再试',
					showCancel: false
				})
				console.log('onError', err);
			})
			this.socketTask.onMessage((res) => {
				this.msg = res.data
				console.log('onMessage', res)
			})
			this.socketTask.onClose((res) => {
				this.connected = false
				this.startRecive = false
				this.socketTask = false
				this.msg = false
				console.log('onClose', res)
			})
			console.log('task', this.socketTask)
		},
		send() {
			this.socketTask.send({
				data: 'from ' + platform + ' : ' + parseInt(Math.random() * 10000).toString(),
				success(res) {
					console.log(res);
				},
				fail(err) {
					console.log(err);
				}
			})
		},
		close() {
			if (this.socketTask && this.socketTask.close) {
				this.socketTask.close()
			}
		},
		search(e) { },
		input(e) { },
	}
}
</script>


<style lang="scss" scoped scoped>
page,
view {
	/* display: flex; */
}

page {

	min-height: 100%;
	background-color: #FFFFFF;

}

.titleText {
	display: -webkit-box;
	/*弹性伸缩盒子模型显示*/
	-webkit-box-orient: vertical;
	/*排列方式*/
	-webkit-line-clamp: 1;
	/*显示文本行数(这里控制多少行隐藏)*/
	overflow: hidden;
	/*溢出隐藏*/
	width: 50%;
}

.red-dot-text::before {
	content: "●";
	/* 使用圆点符号作为文字前的标记 */
	color: red;
	/* 设置圆点的颜色为红色 */
	display: inline-block;
	width: 1em;
	/* 调整圆点的位置 */
	font-size: 1em;
	position: relative;
	top: -25%;
	left: -150%;
}

.riskAssessment {
	width: 100%;
	// background-color: #FFFFFF;
	background-color: #f2f2f2;
	// padding-top: env(safe-area-inset-top); /* 兼容 iOS 11.0-11.4 */
	// padding-top: constant(safe-area-inset-top); /* 兼容 iOS 12.0-12.1 */
	position: fixed;
	top: env(safe-area-inset-top);
	/* 兼容 iOS 11.0-11.4 */
	top: constant(safe-area-inset-top);
	/* 兼容 iOS 12.0-12.1 */
	left: 0;
	touch-action: none;
}

.infor-list {
	display: block;
	width: 100%;
	// height: 80%;
	// height: 676px;
	box-sizing: border-box;
	padding: 3%;
	background-color: #f2f2f2;
	overflow: auto;

	.list {
		display: flex;
		width: 100%;
		height: auto;
		// min-height: 48px;
		border-bottom: 10px solid #eee;
		border-radius: 5px;
		background-color: #FFFFFF;
		padding: 3%;
		box-sizing: border-box;

		.img {
			width: 96upx;
			height: 96upx;
			padding-right: 24upx;
		}

		.list-part {
			flex: 1;

			.title-time {
				display: flex;
				justify-content: space-between;
				position: relative;
				flex-direction: column;
				margin-bottom: 10px;

				text:first-of-type {
					flex: 1;
					color: #272727;
					font-size: 14px;
				}

			}
		}
	}
}

::v-deep .u-icon__icon {
	font-size: 24px !important;
}

::v-deep .u-popup__content {
	height: 50% !important;
	width: 100%;
}
</style>