import { http } from '@/common/service/service.js'
import store from '@/store/index.js';
export const getAllMessageList  = () => {
	let params ={
		pageNo: 0,
		pageSize:10,
	}
	// 方便以后写调查询消息接口
	 	http.get(`/testService/qaTestCalendar/getMyAnnouncementSend`, {
			params: params
		}).then(res => {
			store.dispatch('changeMessageNum',res.data.code)
			if(store.state.messageNum>0){
				uni.setTabBarBadge({
					index: 1,
					text: store.state.messageNum>99 ? '99+' :store.state.messageNum.toString(),
				})
			}else{
					uni.removeTabBarBadge({
					  index: 1,
					})
				}
		})
	}
 